import React, { useEffect, useMemo, useRef, useState } from "react";
import clsx from "clsx";
import { useGetLocale } from "providers";

import { GetCoinImg, LinkWithSearch, LinkWithSearchProps } from "components";
import { crashSocket, replaceNumbers, toFixed } from "utils";
import { useWindowSize } from "providers";
import { useCoins } from "hooks";

import { useUser } from "providers/User";

import { crashSocketsList } from "pages/Crash/crashSocketWatcher";

import css from "./style.module.scss";
import { Bet } from "./types";
import { Bets } from "pages/Crash/crashTypes";
import { createProfitTableListener } from "./createTableListener";
import { useController } from "pages/Crash/controller";

interface Props {
  gameHeight: number;
}

export interface RenderState {
  betData: {
    userAmount: number;
    userWins: number;
  };
  renderUsers: Bet[];
  isCrashed: boolean;
}

const betData = {
  userAmount: 0,
  userWins: 0,
};

export const ProfitTable = ({ gameHeight }: Props) => {
  const intl = useGetLocale();
  const { manager } = useController();
  const { user: activeUser } = useUser();
  const { selectedCoin, coinsPrices, showInUSD } = useCoins([
    "selectedCoin",
    "coinsPrices",
    "showInUSD",
  ]);
  const { width } = useWindowSize();
  const [tableList, setTableList] = useState<Bets>({});

  const statsRef = useRef<HTMLParagraphElement>(null);

  useEffect(() => {
    const watcher = createProfitTableListener(
      setTableList,
      manager,
      activeUser,
    );
    crashSocketsList.profitTableListener = watcher;

    return () => {
      crashSocketsList.profitTableListener = () => null;
    };
  }, [activeUser, selectedCoin?.coin]);

  const tableStyle = useMemo(() => {
    if (gameHeight) {
      if (width < 1280) {
        return {
          height: gameHeight,
        };
      } else {
        return {
          maxHeight: gameHeight,
        };
      }
    } else {
      return {};
    }
  }, [gameHeight, width]);

  useEffect(() => {
    crashSocket.send({ type: "get_current_bets" });
  }, []);

  return (
    <div className={css.box}>
      <div className={css.table}>
        <span className={css.tableColumn}>
          <span className={clsx(css.tableRow, css.userAmountWrapper)}>
            <p className={css.title}>
              {intl.formatMessage({ id: "crashInterface.table.player" })}
            </p>
            {betData.userAmount > 0 ? (
              <p className={clsx(css.title, css.userAmount)} ref={statsRef} />
            ) : null}
          </span>

          <span className={css.tableRow}>
            <p className={css.title}>@</p>
          </span>

          <span className={css.tableRow}>
            <p className={css.title}>
              {intl.formatMessage({ id: "crashInterface.table.bet" })}
            </p>
          </span>
          <span className={css.tableRow}>
            <p className={css.title}>
              {intl.formatMessage({ id: "crashInterface.table.profit" })}
            </p>
          </span>
        </span>
      </div>

      <div className={css.tableWrapper} style={tableStyle}>
        {Object.values(tableList)
          .sort((pFirst, pSecond) => {
            betData.userWins = 0;

            if (pFirst.userId === activeUser?.user_id) {
              // active User must be in up
              return -1;
            } else if (pSecond.userId === activeUser?.user_id) {
              return 1;
            }

            if (pFirst.cashoutAt) {
              const firstPlayerCashout = Number(pFirst.cashoutAt);
              const secondPlayerCashout = pSecond.cashoutAt
                ? Number(pSecond.cashoutAt)
                : Infinity;

              if (firstPlayerCashout < secondPlayerCashout) {
                return 1; //down
              }
            }

            return -1; //up
          })
          .map((player, i, src) => {
            const {
              betAmount: bet_amt,
              coin,
              userId: user_id,
              userName: user_name,
              cashoutAt: cashed_out_at,
              profit,
            } = player;
            const fail = !!(profit && profit === -1);
            const linkSearch: LinkWithSearchProps = {
              search: { name: "page", value: "profile" },
              searchParams: {
                user: user_name,
              },
            };

            if (!fail && profit) {
              betData.userWins++;
            }

            if (i === src.length - 1) {
              betData.userAmount = src.length;
            }

            if (statsRef.current) {
              statsRef.current.innerHTML = `(${betData.userWins}/${betData.userAmount})`;
            }

            return (
              <div
                key={`${user_id}_${user_name}`}
                className={clsx(css.tableColumn, {
                  [css.textFail]: fail,
                  [css.textProfit]: profit && !fail,
                })}>
                <span className={css.tableRow}>
                  <LinkWithSearch {...linkSearch}>
                    <span
                      className={clsx(css.tableRow, css.userName, css.text)}>
                      {user_name}
                    </span>
                  </LinkWithSearch>
                </span>

                <span className={css.tableRow}>
                  <span className={css.text}>
                    {cashed_out_at ? cashed_out_at + "x" : "-"}
                  </span>
                </span>

                <span className={css.tableRow}>
                  <GetCoinImg coin={coin} className={css.btcIcon} />
                  <span className={`${css.text} ${css.textCoin}`}>
                    {showInUSD
                      ? `$${replaceNumbers(
                          (
                            Number(bet_amt) * Number(coinsPrices[coin]?.price)
                          ).toFixed(8),
                        )}`
                      : toFixed(bet_amt)}
                  </span>
                </span>

                <span className={css.tableRow}>
                  {profit && profit > 0 && (
                    <GetCoinImg coin={coin} className={css.btcIcon} />
                  )}
                  <span className={`${css.text} ${css.textCoin}`}>
                    {profit && profit > 0
                      ? showInUSD
                        ? `$${replaceNumbers(
                            (
                              Number(profit) * Number(coinsPrices[coin]?.price)
                            ).toFixed(8),
                          )}`
                        : toFixed(profit)
                      : "-"}
                  </span>
                </span>
              </div>
            );
          })}
      </div>
      <div className={css.shadow} />
    </div>
  );
};
