import React, { FC, useEffect } from "react";

import { coinsManager, useCoins } from "hooks";
import { crashSocket } from "utils";

import { useController } from "pages/Crash/controller";
import { useOnline } from "providers";

const Listeners: FC = ({ children }) => {
  const { selectedCoin, showInUSD } = useCoins();
  const { manager } = useController();

  useEffect(() => {
    if (selectedCoin) {
      const isLimitsExist = !!manager?.fields.general.coinData?.minimum_bet;
      const managerCoinName = manager?.fields.general.coinSelected.name;
      if (isLimitsExist && managerCoinName !== selectedCoin.coin) {
        crashSocket.send({
          type: "get_coin_parameters",
          coin: selectedCoin?.coin,
        });
      }

      if (coinsManager.selectedCoin) {
        manager?.setSelectedCoin(
          Number(coinsManager.selectedCoin.balance),
          coinsManager.selectedCoin.coin,
        );
      }
    }
  }, [selectedCoin?.coin, showInUSD]);

  return <>{children}</>;
};

export default Listeners;
