import React, { FC } from "react";
import TrendsInfo from "./Content";
interface Props {
  onClose: () => void;
  open: boolean;
}

const ModalLazyWrapper: FC<Props> = ({ onClose, open }) => {
  return <>{open && <TrendsInfo onClose={onClose} open={open} />}</>;
};

export default ModalLazyWrapper;
