import React, {
  Dispatch,
  FC,
  SetStateAction,
  useEffect,
  useLayoutEffect,
  useRef,
  useState,
} from "react";
import { install } from "resize-observer";

import { useUser, useWindowSize } from "providers";

import OptionsButton from "components/OptionsButton";
import { resetFocus } from "utils/resetFocus";

import { useController } from "pages/Crash/controller";

import Listeners from "./components/Listeners";
import { Control, Params } from "./components";

install();
interface Props {
  setGameHeight: Dispatch<SetStateAction<number>>;
  setIsLiveStatOpen: Dispatch<SetStateAction<boolean>>;
  setIsHotkeyOpen: Dispatch<SetStateAction<boolean>>;
  setIsSoundOpen: Dispatch<SetStateAction<boolean>>;
  setIsBettingInfoOpen: () => void;

  isLiveStatOpen: boolean;
  isHotkeyOpen: boolean;
  isSoundOpen: boolean;
}

const Panel: FC<Props> = ({
  setIsLiveStatOpen,
  setIsHotkeyOpen,
  setGameHeight,
  setIsSoundOpen,
  setIsBettingInfoOpen,
  isLiveStatOpen,
  isHotkeyOpen,
  isSoundOpen,
}) => {
  const { manager } = useController();
  const { user } = useUser();

  const [blockHeight, setBlockHeight] = useState<number>();
  const [gameDiv, setGameDiv] = useState<HTMLDivElement | null>(null);

  const { width } = useWindowSize();

  const handleLiveStatChange = () => {
    setIsLiveStatOpen(!isLiveStatOpen);
  };
  const handleHotKeysChange = () => {
    resetFocus();
    setIsHotkeyOpen(!isHotkeyOpen);
  };
  const handleSoundChange = () => {
    resetFocus();
    setIsSoundOpen(!isSoundOpen);
  };
  useEffect(() => {
    if (gameDiv) {
      const element = gameDiv;
      let blockHeight: number;
      const resizeObserver = new ResizeObserver((entries) => {
        const currentBlockHeight = entries[0]?.contentRect.height;
        if (currentBlockHeight && currentBlockHeight !== blockHeight) {
          setBlockHeight(currentBlockHeight);
          blockHeight = currentBlockHeight;
        }
      });
      resizeObserver.observe(element);
      return () => resizeObserver.unobserve(element);
    }
  }, [gameDiv]);

  useEffect(() => {
    const crashGameHeight = 500;
    if (blockHeight && width > 1280) {
      setGameHeight(blockHeight + crashGameHeight);
    } else {
      setGameHeight(300);
    }
  }, [blockHeight]);

  useLayoutEffect(() => {
    manager?.initCrashSocket();
    manager?.setActiveUserId(user?.user_id || -1);

    return () => {
      manager?.closeCrashSocket();
    };
  }, [user?.user_name]);

  return (
    <>
      <Listeners>
        <div ref={(node) => setGameDiv(node)}>
          <Control />
          <OptionsButton
            withSound
            soundOnClick={handleSoundChange}
            liveStatOnclick={handleLiveStatChange}
            hotKeysOnClick={handleHotKeysChange}
            bettingInformationOnClick={setIsBettingInfoOpen}
          />
          <Params />
        </div>
      </Listeners>
    </>
  );
};

export default Panel;
