import React, { Dispatch, FC, SetStateAction } from "react";
import clsx from "clsx";

import { useWindowSize } from "providers";

import { TrendsElement } from "pages/Crash/components/History/types";

import { positivePayout } from "../../../History/StoryElement";
import { maxRowNumber } from "../../";

import { cellSize, mCellSize } from "./cellConfig";
import css from "./style.module.scss";

interface Props {
  elements: TrendsElement;
  setOpenedStoryId: Dispatch<SetStateAction<string | null>>;
}

const Column: FC<Props> = ({ elements, setOpenedStoryId }) => {
  const { width } = useWindowSize();
  const { length, value } = elements;

  if (!value) {
    return null;
  }

  return (
    <div className={css.root}>
      {value.map(({ crash_point, id, seed }, i) => {
        if (i < length) {
          return (
            <React.Fragment key={`${id}_${seed}`}>
              <div
                className={clsx(css.cell)}
                onClick={() => setOpenedStoryId(id)}>
                <span
                  className={clsx(
                    css.dot,
                    {
                      [css.red]: Number(crash_point) < positivePayout,
                    },
                    {
                      [css.green]: Number(crash_point) >= positivePayout,
                    },
                  )}
                />
              </div>
            </React.Fragment>
          );
        } else {
          const bottom = maxRowNumber - length;
          const left = i - length + 1;
          const marginY = 2.5;
          const marginX = 2;
          const calcCellSize = width < 600 ? mCellSize : cellSize;
          return (
            <React.Fragment key={`${id}_${seed}`}>
              <div
                className={clsx(css.muchNumber, css.cell)}
                style={{
                  bottom: bottom * calcCellSize + bottom * marginY,
                  left: left * calcCellSize + left * marginX,
                }}>
                <span
                  className={clsx(
                    css.dot,
                    {
                      [css.red]: Number(crash_point) < positivePayout,
                    },
                    {
                      [css.green]: Number(crash_point) >= positivePayout,
                    },
                  )}
                />
              </div>
            </React.Fragment>
          );
        }
      })}
    </div>
  );
};

export default Column;
