import React, {
  Dispatch,
  FC,
  SetStateAction,
  useEffect,
  useRef,
  useState,
} from "react";
import clsx from "clsx";
import { useGetLocale } from "providers";

import { createTimeouts } from "utils";
import { useWhiteTheme, useWindowSize } from "providers";

import { cellSize, mCellSize } from "./components/Column/cellConfig";
import Column from "./components/Column";

import { sortHistory } from "./utils/sortHistory";
import { HistoryElement } from "../../types";
import css from "./style.module.scss";

interface Props {
  setOpenedStoryId: Dispatch<SetStateAction<string | null>>;
  setIsTrendsInfoOpen: Dispatch<SetStateAction<boolean>>;

  isOpen: boolean;

  history: HistoryElement[];
  setHistory: (value: HistoryElement[]) => void;
}

export const maxRowNumber = 7;
const { clearTimeouts, pushTimeout } = createTimeouts();

const Trends: FC<Props> = ({
  setOpenedStoryId,
  setIsTrendsInfoOpen,

  isOpen,
  history,
  setHistory,
}) => {
  const intl = useGetLocale();
  const { width } = useWindowSize();
  const { isWhiteThemeActive } = useWhiteTheme();
  const rowRef = useRef<HTMLDivElement | null>(null);

  const [isEnable, setIsEnable] = useState(isOpen);
  const [maxCellNumber, setMaxCellNumber] = useState(32);

  const changeMaxCellNumber = (rootDiv: HTMLDivElement) => {
    rootDiv.style.maxWidth = "";
    rootDiv.style.width = "";
    let cSize = cellSize;
    if (width < 600) {
      cSize = mCellSize;
    }
    const calcWidth = rootDiv.offsetWidth;
    const cellAmount = Math.floor(calcWidth / (cSize + 2));
    const calcRootWidth = cellAmount * (cSize + 2);

    rootDiv.style.maxWidth = `${calcRootWidth}px`;
    rootDiv.style.width = `${calcRootWidth}px`;

    if (cellAmount !== maxCellNumber) {
      setMaxCellNumber(cellAmount);
    }
  };

  useEffect(() => {
    if (rowRef.current) {
      changeMaxCellNumber(rowRef.current);
    }
  }, [width]);

  useEffect(() => {
    clearTimeouts();
    if (isOpen) {
      setIsEnable(true);
    } else {
      pushTimeout(() => {
        setIsEnable(false);
      }, 500);
    }
  }, [isOpen]);

  const handleInfoOpen = () => {
    setIsTrendsInfoOpen(true);
  };

  if (!isEnable) {
    return null;
  }

  const { deleteIndex, dots } = sortHistory(history, maxCellNumber);

  if (deleteIndex) {
    const newHistory = [...history];
    newHistory.splice(0, deleteIndex);

    setHistory(newHistory);
  }

  return (
    <>
      <div
        className={clsx(
          css.root,
          css.root__background,
          isWhiteThemeActive && css.root__background_white,
        )}>
        <div
          className={clsx(
            css.row,
            css.row__background,
            isWhiteThemeActive && css.row__background_white,
          )}
          ref={(node) => {
            if (node) {
              changeMaxCellNumber(node);
              rowRef.current = node;
            }
          }}>
          {dots.map((list, i) => (
            <React.Fragment key={`key${list.length}_${i}`}>
              <Column elements={list} setOpenedStoryId={setOpenedStoryId} />
            </React.Fragment>
          ))}
        </div>
        <div className={css.info} onClick={handleInfoOpen}>
          <span className={css.info_text}>
            {intl.formatMessage({
              id: "gameTemplate.statistics.historyTable.trends.help.title",
            })}
          </span>
          <div className={css.info_icon}></div>
        </div>
      </div>
    </>
  );
};

export default Trends;
