import React from "react";
import { useGetLocale } from "providers";

import { Dialog } from "containers/PopUps";

import DialogHeader from "./DialogHeader";
import ExampleColumn from "./ExampleColumn";
import ExampleText from "./ExampleText";
import { positivePayout } from "../History/StoryElement";
import css from "./style.module.scss";

interface Props {
  onClose: () => void;
  open: boolean;
}

const exampleData = {
  first: [[2.51, 3.17, 2.19], [1.11, 1.34], [3.74], [], [], []],
  second: [
    [3.11, 4.25, 13.81, 2.25, 11.42, 15.8, 1.91],
    [10.03, 41.14],
    [1.12, 1.45, 1.81],
    [10.74],
    [],
    [],
    [],
  ],
};

const TrendsInfo = ({ onClose, open }: Props) => {
  const intl = useGetLocale();

  return (
    <Dialog
      onClose={onClose}
      isOpen={open}
      paper={{
        className: css.dialog,
      }}>
      <DialogHeader
        title={intl.formatMessage({
          id: "gameTemplate.statistics.historyTable.trends.help.title",
        })}
        onClose={onClose}
      />
      <div className={css.root}>
        <h4 className={css.title}>
          {intl.formatMessage({
            id: "gameTemplate.statistics.historyTable.trends.help.explanation",
          })}
        </h4>
        <div className={css.container}>
          <div className={css.dataWrapper}>
            <div className={css.columnWrapper}>
              {exampleData.first.map((value, i) => (
                <React.Fragment key={`@key=${i}`}>
                  <ExampleColumn data={value} />
                </React.Fragment>
              ))}
            </div>
            <div className={css.textWrapper}>
              <ExampleText data={exampleData.first} />
            </div>
          </div>
          <div className={css.dataWrapper}>
            <div className={css.columnWrapper}>
              {exampleData.second.map((value, i, src) => {
                const prevNumber = i > 0 && src[i - 1][0] ? src[i - 1][0] : 0;

                const isRevert =
                  prevNumber >= positivePayout && value[0] >= positivePayout;

                return (
                  <React.Fragment key={`@key=${i}`}>
                    <ExampleColumn data={value} isRevert={isRevert} />
                  </React.Fragment>
                );
              })}
            </div>

            <div className={css.textWrapper}>
              <ExampleText data={exampleData.second} />
            </div>
          </div>
        </div>
      </div>
    </Dialog>
  );
};

export default React.memo(TrendsInfo);
