import React, { Dispatch, SetStateAction, useCallback } from "react";
import css from "./style.module.scss";

type Props = {
  payout: number | string;
  setOpenedStoryId: Dispatch<SetStateAction<string | null>>;
  id: string;
};

export const positivePayout = 1.9;

export const StoryElement = React.memo(
  ({ payout, setOpenedStoryId, id }: Props) => {
    const openRoundData = useCallback(() => {
      setOpenedStoryId(id);
    }, [id]);

    return (
      <div className={css.elementBox} onClick={openRoundData}>
        <div
          className={`${css.color} ${
            payout < positivePayout ? css.colorRed : css.colorGreen
          }`}
        />
        <p
          className={`${css.payout} ${
            payout < positivePayout ? css.payoutRed : css.payoutGreen
          }`}>
          {Number(payout).toFixed(2)}x
        </p>
      </div>
    );
  },
);
