import React, {
  Dispatch,
  FC,
  SetStateAction,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import clsx from "clsx";

import { useWindowSize } from "providers";

import { StoryElement } from "./StoryElement";
import { HistoryElement } from "../../types";
import css from "./style.module.scss";
import { track } from "utils";

let storyUnitWidth = 50;
const marginStyleData = [
  [1850, 25],
  [1670, 20],
  [1550, 15],
  [1400, 10],
  [1281, 5],
  [1200, 15],
  [1100, 10],
  [1000, 5],
  [960, 1],
  [800, 25],
  [700, 20],
  [600, 10],
  [550, 10],
  [400, 5],
  [0, 5],
];

const marginData = [...marginStyleData];
const animationTime = 1000;

const timeouts: NodeJS.Timeout[] = [];
const clearTimeouts = () => {
  timeouts.forEach((time) => {
    clearTimeout(time);
  });
  timeouts.length = 0;
};

const createAnimation = (
  storyUnitWidth: number,
  margin: number,
  isMoved: boolean,
) => {
  if (isMoved) {
    return {
      transition: `transform ${animationTime}ms`,
      transform: `translateX(-${storyUnitWidth + margin * 2}px)`,
    };
  }

  return {};
};

interface Props {
  setOpenedStoryId: Dispatch<SetStateAction<string | null>>;
  startedRounds: HistoryElement[];
  newElement: HistoryElement | null;
  clearNewElement: () => void;
}

const History: FC<Props> = ({
  setOpenedStoryId,
  newElement,
  clearNewElement,
  startedRounds,
}) => {
  const { width } = useWindowSize();

  const slideRef = useRef<HTMLDivElement>(null);

  const [storyBoxWidth, setStoryBoxWidth] = useState(620 < width ? 620 : width);
  const [margin, setMargin] = useState(10);
  const [storyData, setStoryData] = useState<HistoryElement[]>([]);
  const [isMoved, setIsMoved] = useState(false);

  const [boxWidth, setBoxWidth] = useState(620 < width ? 620 : width);

  const changeBoxWidth = () => {
    for (const [componentWidth, componentMargin] of marginData) {
      if (width >= componentWidth) {
        setMargin(componentMargin);
        break;
      }
    }

    let instalWidth = storyBoxWidth;

    try {
      if (slideRef.current && slideRef.current?.children?.length) {
        const firstElement: number =
          Array.prototype.slice
            .call(slideRef.current.children)
            .find(
              (div: HTMLDivElement) => getComputedStyle(div).display !== "none",
            )?.offsetWidth || 40;

        const refWidth = slideRef.current.offsetWidth;
        if (firstElement) {
          storyUnitWidth = Math.floor(firstElement);
        }
        if (refWidth) {
          instalWidth = Math.floor(refWidth);

          setStoryBoxWidth(instalWidth);
        }
      }
    } catch (error) {
      instalWidth = width - 40;
      setStoryBoxWidth(instalWidth);
      track()?.errors(error);
    }

    setBoxWidth(Math.floor(instalWidth));
  };

  useEffect(() => {
    if (!isMoved) {
      changeBoxWidth();
    }
  }, [isMoved]);

  useEffect(() => {
    changeBoxWidth();
  }, [width]);

  const slider = (newElement: HistoryElement) => {
    const { crash_point, seed, id } = newElement;
    setIsMoved(true);
    storyData.push({
      crash_point,
      seed,
      id,
    });

    const t = setTimeout(async () => {
      storyData.shift();
      setIsMoved(false);
      const t = setTimeout(() => {
        setBoxWidth(Math.floor(storyBoxWidth));
      }, 300);
      timeouts.push(t);
    }, animationTime);

    timeouts.push(t);
  };

  useEffect(() => {
    if (startedRounds.length) {
      if (startedRounds.length > 10) {
        setStoryData(startedRounds.splice(-10, startedRounds.length));
      } else {
        setStoryData(startedRounds);
      }

      const t = setTimeout(() => {
        changeBoxWidth();
      }, 0);

      timeouts.push(t);
    }

    return () => clearTimeouts();
  }, [startedRounds]);

  useEffect(() => {
    if (newElement) {
      slider(newElement);
      clearNewElement();
    }
  }, [newElement]);

  return (
    <div className={clsx(css.historyWrapper)} style={{ width: boxWidth }}>
      <div
        ref={slideRef}
        className={css.sliderWrapper}
        style={createAnimation(storyUnitWidth, margin, isMoved)}>
        {storyData.map(({ crash_point, seed, id }) => (
          <React.Fragment key={seed}>
            <StoryElement
              payout={crash_point}
              setOpenedStoryId={setOpenedStoryId}
              id={id}
            />
          </React.Fragment>
        ))}
      </div>
    </div>
  );
};

export default History;
