import { useEffect, useState } from "react";
import { ButtonValues, useController } from "pages/Crash/controller";

import { buttonTextValues } from "./buttonTextValues";

export const useButtonValue = () => {
  const { manager } = useController();

  const [buttonText, setButtonText] = useState<string[]>(
    buttonTextValues[ButtonValues.Bet_nextRound],
  );

  useEffect(() => {
    const removeListener = manager?.betButtonStatusListener((status) => {
      setButtonText(buttonTextValues[status]);
    });

    return () => {
      removeListener && removeListener();
    };
  }, []);

  return buttonText;
};
