import React, { ChangeEvent, FC, useEffect, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { Button, GetCoinImg, Warning } from "components";
import { useCoins } from "hooks";

import { useHotKeyListener } from "components/HotKeys/Listener";

import { CrashInput } from "../CrashInput";
import {
  fields,
  ManualFieldsValues,
  useController,
} from "pages/Crash/controller";
import css from "./style.module.scss";

interface Props {
  wrap?: boolean;
  disabled?: boolean;
}

const MainInput: FC<Props> = ({ wrap, disabled }) => {
  const intl = useGetLocale();

  const { showInUSD, selectedCoin } = useCoins(["showInUSD", "selectedCoin"]);

  const [warningOpen, setWarningOpen] = useState(false);

  const { manager } = useController();
  const [manual, setManual] = useState<ManualFieldsValues>(
    manager?.fields.manual || fields.manual,
  );

  const [usdValue, setUsdValue] = useState(
    (Number(manual.betAmount) * Number(selectedCoin?.price || 0)).toFixed(8),
  );

  useEffect(() => {
    const returnMListener = manager?.manualValuesListener((values) => {
      setManual({
        ...values,
        betAmount: values.betAmount,
        profit: Number(values.profit).toFixed(8),
      });
    });

    return () => {
      returnMListener && returnMListener();
    };
  }, []);

  const calcUsdAmount = (amount: string | number) =>
    (Number(amount) * Number(selectedCoin?.price)).toFixed(8);

  const setAmountValue = (amount: string, isOnChange?: boolean) => {
    let cryptAmount = amount;
    let usdAmount = calcUsdAmount(amount);

    if (showInUSD && selectedCoin?.coin !== "GEM") {
      usdAmount = amount;
      cryptAmount = Math.max(
        Number(amount) / Number(selectedCoin?.price),
        Number(manager?.fields.general.coinData?.minimum_bet),
      ).toFixed(8);
    }

    setUsdValue(usdAmount);

    if (isOnChange) {
      setManual({
        ...manual,
        betAmount: cryptAmount,
      });
    } else {
      manager?.setManualField("betAmount", cryptAmount);
    }
  };

  useEffect(() => {
    if (showInUSD) {
      if (selectedCoin?.coin !== "GEM") {
        setUsdValue(calcUsdAmount(manual.betAmount));
      } else {
        setUsdValue(manual.betAmount as string);
      }
    }
  }, [manual.betAmount, showInUSD]);

  const handleChangeAmount = (ev: ChangeEvent<HTMLInputElement>) => {
    const value = Number(ev.target.value);

    if (value || value === 0) {
      setAmountValue(ev.target.value, true);
    }
  };

  const handleBlurAmount = () => {
    let { maximum_bet = 0, minimum_bet = 0 } =
      manager?.fields.general.coinData || {};

    let amount = Number(manual.betAmount);
    let balance = Number(selectedCoin?.balance) || 0;

    if (showInUSD && selectedCoin?.coin !== "GEM") {
      amount = amount * Number(selectedCoin?.price);
      balance = balance * Number(selectedCoin?.price);
      maximum_bet = maximum_bet * Number(selectedCoin?.price);
      minimum_bet = minimum_bet * Number(selectedCoin?.price);
    }

    amount = Math.max(amount, minimum_bet);
    amount = Math.min(amount, maximum_bet);

    if (amount > balance) {
      amount = minimum_bet;
    }

    setAmountValue(amount.toFixed(8));
  };

  const handleMinAmount = () => {
    let minBet = String(manager?.fields.general.coinData?.minimum_bet) || "0";
    if (showInUSD && selectedCoin?.coin !== "GEM") {
      minBet = (Number(minBet) * Number(selectedCoin?.price)).toFixed(8);
    }
    setAmountValue(Number(minBet).toFixed(8));
  };

  const handleMaxAmount = () => {
    let maxBet = manager?.fields.general.coinData?.maximum_bet;
    let balanceAmount = manager?.fields.general.coinSelected.balance;

    if (balanceAmount && maxBet) {
      if (showInUSD && selectedCoin?.coin !== "GEM") {
        maxBet *= Number(selectedCoin?.price);
        balanceAmount *= Number(selectedCoin?.price);
      }

      const value = Math.min(maxBet, balanceAmount).toFixed(8);
      setAmountValue(value);
    }
  };

  const handleCalcAmount = (type: "multiply" | "divide") => {
    let newValue =
      type === "multiply"
        ? Number(manual.betAmount) * 2
        : Number(manual.betAmount) / 2;

    const { maximum_bet: maxBet = "0", minimum_bet: minBet = "0" } =
      manager?.fields.general.coinData || {};
    const { balance: amount = "0" } =
      manager?.fields.general.coinSelected || {};

    switch (type) {
      case "multiply":
        newValue = Math.min(Number(newValue), Number(maxBet), Number(amount));
        break;

      default:
      case "divide":
        newValue = Math.max(Number(newValue), Number(minBet));
        break;
    }

    if (showInUSD && selectedCoin?.coin !== "GEM") {
      newValue *= Number(selectedCoin?.price);
    }

    return newValue.toFixed(8);
  };

  const handleMultiplyAmount = () => {
    setAmountValue(handleCalcAmount("multiply"));
  };

  const handleDivideAmount = () => {
    setAmountValue(handleCalcAmount("divide"));
  };

  const handleWarningOpen = () => {
    setWarningOpen(true);
  };

  const handleWarningClose = () => {
    setWarningOpen(false);
  };

  useHotKeyListener({
    callbackKeyA: handleDivideAmount,
    callbackKeyS: handleMultiplyAmount,
  });

  return (
    <div className={css.box}>
      <div className={css.inputWrapper}>
        <CrashInput
          type="number"
          className={clsx(css.input, {
            [css.inputMove]: showInUSD && selectedCoin?.coin !== "GEM",
          })}
          value={
            showInUSD && selectedCoin?.coin !== "GEM"
              ? usdValue
              : manual.betAmount
          }
          onChange={handleChangeAmount}
          onBlur={handleBlurAmount}
          disabled={disabled}
        />
        <GetCoinImg />
        {showInUSD && selectedCoin?.coin !== "GEM" && (
          <span className={css.dollarLabel}>$</span>
        )}
      </div>
      <div
        className={clsx(css.buttonBox, {
          [css.buttonBox_mini]: wrap,
        })}>
        <Button
          disabled={disabled}
          className={css.button}
          onClick={handleMinAmount}>
          {intl.formatMessage({
            id: "crashInterface.bet.menu.buttons.min",
          })}
        </Button>
        <Button
          disabled={disabled}
          className={css.button}
          onClick={handleDivideAmount}>
          {intl.formatMessage({
            id: "crashInterface.bet.menu.buttons.half",
          })}
        </Button>
        <Button
          disabled={disabled}
          className={css.button}
          onClick={handleMultiplyAmount}>
          {intl.formatMessage({
            id: "crashInterface.bet.menu.buttons.double",
          })}
        </Button>
        <Button
          disabled={disabled}
          className={css.button}
          onClick={handleWarningOpen}>
          {intl.formatMessage({
            id: "crashInterface.bet.menu.buttons.max",
          })}
        </Button>

        <Warning
          isOpen={warningOpen}
          onClose={handleWarningClose}
          callBack={handleMaxAmount}
        />
      </div>
    </div>
  );
};

export default MainInput;
