import React, { FC, Suspense, useEffect, useState } from "react";

import LazyComponentLoader from "config/LazyComponentLoader";
import ProgressFallback from "components/fallbacks/Progress";

import { Manual } from "./components";
import { useController } from "pages/Crash/controller";

const Auto = React.lazy(() => LazyComponentLoader(import("./components/Auto")));

const Params: FC = () => {
  const { manager } = useController();
  const [isAuto, setIsAuto] = useState(manager?.fields.auto.data.isOpen);

  useEffect(() => {
    const removeIsOpen = manager?.autoIsOpenListener((value) => {
      setIsAuto(value);
    });

    return () => {
      removeIsOpen && removeIsOpen();
    };
  }, []);
  return (
    <div>
      {!isAuto ? (
        <Manual />
      ) : (
        <Suspense fallback={<ProgressFallback color={"#00e7ff"} />}>
          <Auto />
        </Suspense>
      )}
    </div>
  );
};

export default Params;
