import React, { useEffect, useState } from "react";
import clsx from "clsx";

import { useWhiteTheme } from "providers";

import CrashGame from "games/Crash";
import { setIsCrashEffectsActive } from "games/Crash/game/engine/entities/Video";
import LazyComponentLoader from "config/LazyComponentLoader";

import Statistics from "components/Statistics";
import { HotKeysModal } from "components/HotKeys";
import LiveStats from "components/LiveStats";

import Panel from "./components/Panel";
import { History } from "./components/History";
import { ProfitTable } from "./components/Table";

import { CrashHeaderSwitchBox } from "./components";
import { CrashControlProvider } from "./controller";

import css from "./style.module.scss";
import { SoundModals } from "components/SoundModal";
import { track } from "utils";

const BettingInformation = React.lazy(() =>
  LazyComponentLoader(import("pages/ModalPages/pages/BettingInformation")),
);

let isShowEffect = true;

try {
  isShowEffect = JSON.parse(
    localStorage.getItem("isShowCrashEffect") || "true",
  );
} catch (error) {
  isShowEffect = true;
  track()?.errors(error);
}

const CrashTemplate = () => {
  const { isWhiteThemeActive } = useWhiteTheme();

  const [gameHeight, setGameHeight] = useState<number>(0);
  const [isLiveStatOpen, setIsLiveStatOpen] = useState(false);
  const [isSoundOpen, setIsSoundOpen] = useState(false);
  const [isBetInfoOpen, setIsBetInfoOpen] = useState(false);
  const [isHotkeyOpen, setIsHotkeyOpen] = useState(false);
  const [isTrendsOn, setIsTrendsOn] = useState(false);

  const [showGameEffects, setShowGameEffects] = useState<boolean>(isShowEffect);
  useEffect(() => {
    const crashPayoutText: HTMLTitleElement | null = document.getElementById(
      "crash-payout-text",
    ) as HTMLTitleElement;

    if (crashPayoutText) {
      const list = crashPayoutText.classList;
      if (isWhiteThemeActive) {
        if (!list.contains("canvasWhiteVarela")) {
          list.add("canvasWhiteVarela");
        }
      } else {
        if (list.contains("canvasWhiteVarela")) {
          list.remove("canvasWhiteVarela");
        }
      }
    }
  }, [isWhiteThemeActive]);

  const handleLiveStatClose = () => {
    setIsLiveStatOpen(false);
  };

  const handleHotKeysClose = () => {
    setIsHotkeyOpen(false);
  };
  const handleSoundClose = () => {
    setIsSoundOpen(false);
  };

  const changeEffect = (value?: boolean) => {
    const isShow = value || false;
    setShowGameEffects(isShow);
    setIsCrashEffectsActive(isShow);
    localStorage.setItem("isShowCrashEffect", `${value}`);
  };

  return (
    <CrashControlProvider>
      <div className={css.root}>
        <LiveStats isOpen={isLiveStatOpen} handleClose={handleLiveStatClose} />
        <SoundModals isOpen={isSoundOpen} onClose={handleSoundClose} />
        <HotKeysModal isOpen={isHotkeyOpen} onClose={handleHotKeysClose} />
        <BettingInformation
          isOpen={isBetInfoOpen}
          game="crash"
          onClose={() => setIsBetInfoOpen(false)}
        />
        <div className={css.item}>
          <div className={css.gameBlock}>
            <div className={css.gameCell}>
              <History isTrendsOn={isTrendsOn} setIsTrendsOn={setIsTrendsOn} />
              <div className={css.switchWrapper}>
                <CrashHeaderSwitchBox
                  isChecked={showGameEffects}
                  handleChange={changeEffect}
                  text={{ on: "Effects" }}
                />
              </div>
              <div
                className={clsx(css.gameWrapper, {
                  [css.trendsMargin]: isTrendsOn,
                })}>
                <CrashGame isShowEffects={showGameEffects} />
              </div>
              <div className={css.gameFooter}>
                <Panel
                  setIsLiveStatOpen={setIsLiveStatOpen}
                  setIsSoundOpen={setIsSoundOpen}
                  setIsHotkeyOpen={setIsHotkeyOpen}
                  setGameHeight={setGameHeight}
                  isLiveStatOpen={isLiveStatOpen}
                  isHotkeyOpen={isHotkeyOpen}
                  isSoundOpen={isSoundOpen}
                  setIsBettingInfoOpen={() => setIsBetInfoOpen(true)}
                />
              </div>
            </div>
            <ProfitTable gameHeight={gameHeight} />
          </div>
        </div>
        <div className={css.item}>
          <div className={css.statisticsWrapper}>
            <Statistics />
          </div>
        </div>
      </div>
    </CrashControlProvider>
  );
};

export default CrashTemplate;
