import React, { FC } from "react";
import { useGetLocale } from "providers";

import { SwitchBox as Switch } from "components";
import css from "./style.module.scss";

interface Props {
  switchHandleChange: (checked: boolean) => void;
  isChecked: boolean;
}

const SwitchBox: FC<Props> = ({ switchHandleChange, isChecked }) => {
  const intl = useGetLocale();

  return (
    <div className={css.switcher}>
      <Switch
        text={{
          off: intl.formatMessage({ id: "crashInterface.bet.switch.manual" }),
          on: intl.formatMessage({ id: "crashInterface.bet.switch.auto" }),
        }}
        {...{
          switchHandleChange,
          isChecked,
        }}
      />
    </div>
  );
};

export default SwitchBox;
