import React, {
  Dispatch,
  SetStateAction,
  FC,
  useState,
  useEffect,
} from "react";
import clsx from "clsx";
import { useGetLocale } from "providers";

import { CrashStoryListener } from "games/Crash/game/engine/entities/SocketListener/listenerTypes";
import HistoryModal from "components/Statistics/Tabs/History/HistoryModal";
import { crashSocketsList } from "pages/Crash/crashSocketWatcher";
import { HistoryElement } from "components/Statistics/Tabs";

import TrendsInfo from "./components/TrendsInfo";
import HistoryRow from "./components/History";
import Trends from "./components/Trends";
import css from "./style.module.scss";
import { CrashHeaderSwitchBox } from "..";
import { crashSocket } from "utils";

interface Props {
  setIsTrendsOn: Dispatch<SetStateAction<boolean>>;
  isTrendsOn: boolean;
}

interface SocketValues {
  history?: HistoryElement[];
  setHistory?: (value: HistoryElement[]) => void;
}

const socketValues: SocketValues = {};

const socketMethods = {
  add: (element: HistoryElement | HistoryElement[]) => {
    const { history, setHistory } = socketValues;
    if (history && setHistory) {
      //@ts-ignore
      const newHistory: HistoryElement[] = element?.length
        ? element
        : [element];
      setHistory([...history, ...newHistory]);
    }
  },
};

export const History: FC<Props> = ({ setIsTrendsOn, isTrendsOn }) => {
  const intl = useGetLocale();

  const [openedStoryId, setOpenedStoryId] = useState<string | null>(null);
  const [isTrendsInfoOpen, setIsTrendsInfoOpen] = useState(false);
  const [history, setHistory] = useState<HistoryElement[]>([]);
  socketValues.history = history;
  socketValues.setHistory = setHistory;

  const [rowHistory, setRowHistory] = useState<HistoryElement[]>([]);
  const [newRound, setNewRound] = useState<HistoryElement | null>(null);

  const handleHistoryModalClose = () => {
    setOpenedStoryId(null);
  };
  const handleTrendsInfoClose = () => {
    setIsTrendsInfoOpen(false);
  };

  const handleTrends = (isOn?: boolean) => {
    setIsTrendsOn(!!isOn);
  };

  const clearRound = () => {
    setNewRound(null);
  };

  useEffect(() => {
    const roundsListener = (data: CrashStoryListener) => {
      switch (data.method) {
        case "previous_rounds":
          const history = [...data.rounds].reverse(),
            rowHistory = [...history];
          rowHistory.length = 10;
          setRowHistory(rowHistory);
          socketMethods.add(history);
          break;
        case "crashed":
          setNewRound(data.round);
          socketMethods.add(data.round);
          break;

        default:
          break;
      }
    };
    crashSocketsList.crashMainHistoryListener = roundsListener;
    return () => {
      crashSocketsList.crashMainHistoryListener = () => {};
    };
  }, []);

  return (
    <div className={css.root}>
      <HistoryModal id={openedStoryId} onClose={handleHistoryModalClose} />
      <TrendsInfo open={isTrendsInfoOpen} onClose={handleTrendsInfoClose} />
      <div className={css.box}>
        <div className={css.animationWrapper}>
          <HistoryRow
            setOpenedStoryId={setOpenedStoryId}
            startedRounds={rowHistory}
            newElement={newRound}
            clearNewElement={clearRound}
          />
        </div>
        <div className={css.switchWrapper}>
          <CrashHeaderSwitchBox
            isChecked={isTrendsOn}
            handleChange={handleTrends}
            text={{
              off: intl.formatMessage({
                id: "gameTemplate.statistics.historyTable.trends.title",
              }),
            }}
          />
        </div>
        <div className={css.wrapper}>
          <div
            className={clsx(css.trendsWrapper, {
              [css.disable]: !isTrendsOn,
            })}>
            <Trends
              history={history}
              setHistory={setHistory}
              setOpenedStoryId={setOpenedStoryId}
              setIsTrendsInfoOpen={setIsTrendsInfoOpen}
              isOpen={isTrendsOn}
            />
          </div>
        </div>
      </div>
    </div>
  );
};
