import React, { FC } from "react";
import clsx from "clsx";

import { SwitchBox } from "components";
import { useWhiteTheme } from "providers";

import css from "./style.module.scss";

interface Props {
  isChecked: boolean;
  handleChange: (checked: boolean) => void;
  text: {
    on?: string;
    off?: string;
  };
}

export const CrashHeaderSwitchBox: FC<Props> = ({
  handleChange,
  isChecked,
  text,
}) => {
  const { isWhiteThemeActive } = useWhiteTheme();
  return (
    <>
      <SwitchBox
        isChecked={isChecked}
        switchHandleChange={handleChange}
        className={css.root}
        toggleSwitchPosition={"60%"}
        text={{
          off: text.off || "",
          on: text.on || "",
          color: isWhiteThemeActive ? "" : "white",
        }}
        classes={{
          switch: {
            toggleEnabled: css.switchToggleEnabled,
            toggle: css.switchToggle,
          },
        }}
      />
    </>
  );
};
