import React, { FC, useEffect, useState } from "react";
import clsx from "clsx";

import { useWhiteTheme } from "providers/WhiteTheme";

import { maxRowNumber } from "../../Trends";
import { positivePayout } from "../../History/StoryElement";
import css from "./style.module.scss";

interface Props {
  data: number[];
  isRevert?: boolean;
}

const ExampleColumn: FC<Props> = ({ data, isRevert }) => {
  const { isWhiteThemeActive } = useWhiteTheme();

  const [empty, setEmpty] = useState<FC[]>([]);

  const EmptyComponent: FC = () => <div className={clsx(css.cell)} />;

  useEffect(() => {
    if (data.length <= maxRowNumber) {
      const emptyElements: FC[] = [];

      for (let index = 0; index < maxRowNumber - data.length; index++) {
        emptyElements.push(EmptyComponent);
      }
      setEmpty(emptyElements);
    }
  }, [data, isWhiteThemeActive]);

  return (
    <div
      className={clsx(css.root, {
        [css.revert]: isRevert,
      })}>
      {data.map((value, i) => (
        <div
          key={`@${value}_${i}`}
          className={clsx(css.cell, css.activeCell, {
            [css.revertCell]: isRevert,
          })}
          style={
            isRevert
              ? {
                  left: i * 32,
                }
              : {}
          }>
          <div
            className={clsx(
              css.dot,
              {
                [css.red]: value < positivePayout,
              },
              {
                [css.green]: value >= positivePayout,
              },
            )}
          />
        </div>
      ))}
      {!isRevert &&
        empty.map((Component, i, src) => (
          <Component key={`componentID_${i}${src.length}`} />
        ))}
    </div>
  );
};

export default React.memo(ExampleColumn);
