import { throttle } from "utils/lodash";
import { CrashGameManager } from "pages/Crash/controller";
import {
  Bets,
  CurrentBetsState,
  GameBetUser,
  ProfitTableListenerType,
} from "pages/Crash/crashTypes";
import { User } from "providers/User/Context";

export const createProfitTableListener = (
  setList: (value: Bets) => void,
  manager: CrashGameManager | null,
  activeUser: User | null,
) => {
  const interimBets: Bets = {};

  const fillList = () => {
    setList({
      ...interimBets,
    });
  };
  const clearInterimList = () => {
    for (const key in interimBets) {
      delete interimBets[key];
    }
  };
  const wait = throttle((f: () => void) => {
    f();
  }, 300);
  const render = () => {
    wait(() => fillList());
  };
  const setLosses = () => {
    for (const key in interimBets) {
      if (!interimBets[key].profit) {
        interimBets[key].profit = -1;
      }
    }

    fillList();
  };

  const ignoredUserNames = ["hcacw", "hcacw_stream"];

  const fillBets = (list: CurrentBetsState) => {
    for (const id in list) {
      const user = list[id];

      if (
        `${id}` === `${activeUser?.user_id}` &&
        manager &&
        !user.profit
        // !!(user.profit || user.profit !== -1)
      ) {
        const { playableStatus, gameMode } = manager.fields.general;
        if (playableStatus !== "play" && gameMode !== "loading") {
          manager.fields.general.playableStatus = "play";
          manager?.setGameMode(gameMode);
        }
      }

      const isUserMastBeIgnored = ignoredUserNames.includes(user.user_name);

      if (!isUserMastBeIgnored) {
        interimBets[user.user_id] = {
          betAmount: Number(user.bet_amt),
          coin: user.coin,
          userId: user.user_id,
          userName: user.user_name,
          cashoutAt: user.cashed_out_at,
          profit: user.profit,
        };
      }
    }

    render();
  };

  let waitForReset = false;

  return (data: ProfitTableListenerType) => {
    switch (data.method) {
      case "new_bet":
        if (waitForReset) {
          clearInterimList();
          fillList();
          waitForReset = false;
        }

        const newBet: GameBetUser = {
          betAmount: Number(data.bet_amt),
          userId: data.user_id,
          userName: data.user_name,
          coin: data.coin,
        };
        interimBets[data.user_id] = newBet;
        render();

        break;

      case "crashed":
        setLosses();
        waitForReset = true;
        break;

      case "bet_cashedout_update":
        const cashedoutBet = interimBets[data.user_id];
        if (cashedoutBet) {
          cashedoutBet.cashoutAt = data.at;
          cashedoutBet.profit = Number(data.profit);
          render();
        }

        break;
      case "current_bets":
        fillBets(data.bets);
        break;
      default:
        break;
    }
  };
};
