import React, { FC, useEffect, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { Button } from "components";
import { useHotKeyListener } from "components/HotKeys/Listener";

import { ButtonValues, useController } from "pages/Crash/controller";

import { useButtonValue } from "./hooks/useButtonValue";
import SwitchBox from "./components/SwitchBox";
import css from "./style.module.scss";

const Control: FC = () => {
  const intl = useGetLocale();
  const { manager } = useController();
  const [isAuto, setIsAuto] = useState(
    manager?.fields.auto.data.isOpen || false,
  );
  const [isDisabled, setIsDisabled] = useState(false);

  const [bigText, smallText] = useButtonValue();

  useEffect(() => {
    const removeIsOpen = manager?.autoIsOpenListener((value) => {
      setIsAuto(value);
    });
    const removeIsDisable = manager?.disableListener((value) => {
      setIsDisabled(value);
    });

    return () => {
      removeIsDisable && removeIsDisable();
      removeIsOpen && removeIsOpen();
    };
  }, []);

  const handleAutoChange = (checked: boolean) => {
    manager?.setIsAutoModeOpen(checked);
  };

  const placeABet = () => manager?.buttonOnClick();

  useHotKeyListener({
    callbackSpace: () => {
      if (!isDisabled) {
        placeABet();
      }
    },
  });

  const isCashout =
    manager?.fields.general.buttonStatus === ButtonValues.Cashout;
  return (
    <div className={css.root}>
      <SwitchBox isChecked={isAuto} switchHandleChange={handleAutoChange} />

      <Button
        className={css.button}
        disabled={isDisabled}
        waveColor={"black"}
        onClick={placeABet}>
        <div className={css.buttonWrapper}>
          <p className={css.buttonText}>
            {intl.formatMessage({
              id: bigText,
            })}
          </p>

          {!isCashout && (
            <span className={css.helpButtonText}>
              {smallText &&
                intl.formatMessage({
                  id: smallText,
                })}
            </span>
          )}
          <span
            className={clsx(css.helpButtonText, "crash-pay-button")}
            style={
              !isCashout
                ? {
                    display: "none",
                  }
                : undefined
            }
          />
        </div>
      </Button>
    </div>
  );
};

export default Control;
