import React, { ChangeEventHandler, useEffect, useState } from "react";
import { useGetLocale } from "providers";

import { GetCoinImg } from "components";
import { useCoins } from "hooks";

import { CrashInput } from "../../../CrashInput";
import css from "./style.module.scss";
import {
  fields,
  ManualFieldsValues,
  useController,
} from "pages/Crash/controller";
import crashTrackConfig from "pages/Crash/controller/config";
import { toFixed } from "utils";

const coinImgStyle = { zIndex: 3 };

const Params = () => {
  const intl = useGetLocale();

  const { manager } = useController();
  const [manual, setManual] = useState<ManualFieldsValues>(
    manager?.fields.manual || fields.manual,
  );

  const { showInUSD, selectedCoin } = useCoins(["showInUSD", "selectedCoin"]);

  useEffect(() => {
    const returnMListener = manager?.manualValuesListener((values) => {
      setManual({ ...values });
    });

    return () => {
      returnMListener && returnMListener();
    };
  }, []);

  const handleChangePayout: ChangeEventHandler<HTMLInputElement> = ({
    target,
  }) => {
    //@ts-ignore
    manager?.setManualField("payout", target.value);
  };

  const handleBlurPayout = () => {
    let calcPayout = Number(payout);
    if (calcPayout > crashTrackConfig.payout.max) {
      calcPayout = crashTrackConfig.payout.max;
    } else if (calcPayout < crashTrackConfig.payout.min) {
      calcPayout = crashTrackConfig.payout.min;
    }

    manager?.setManualField("payout", Number(toFixed(calcPayout, 2)));
  };

  const { profit, payout, chance: opportunity } = manual;

  return (
    <>
      <div className={css.root}>
        <p className={css.text}>
          {intl.formatMessage({ id: "crashInterface.bet.menu.profit" })}
        </p>
        <div className={css.coinWrapper}>
          <GetCoinImg style={coinImgStyle} />
          <div className={css.inputWrapper}>
            <CrashInput
              className={css.coinInput}
              disabled
              readOnly
              value={
                showInUSD && selectedCoin?.coin !== "GEM"
                  ? `$${(Number(profit) * Number(selectedCoin?.price)).toFixed(
                      8,
                    )}`
                  : toFixed(profit)
              }
            />
          </div>
        </div>
      </div>

      <div className={css.root}>
        <p className={css.text}>
          {intl.formatMessage({ id: "crashInterface.bet.menu.payout" })}
        </p>
        <div className={css.inputWrapper}>
          <CrashInput
            value={payout}
            type="number"
            onChange={handleChangePayout}
            onBlur={handleBlurPayout}
          />
        </div>
      </div>

      <div className={`${css.root} ${css.opportunity}`}>
        <p className={css.text}>
          {intl.formatMessage({ id: "crashInterface.bet.menu.opportunity" })}
        </p>
        <div className={css.percent}>{`${opportunity}%`}</div>
      </div>
    </>
  );
};

export default Params;
