import { HistoryElement, SortedTrendsValue } from "../../../types";
import { positivePayout } from "../../History/StoryElement";
import { maxRowNumber } from "..";

export const sortHistory = (
  noSorted: HistoryElement[],
  maxCellNumber: number,
): SortedTrendsValue => {
  let iterator = 0;
  const sortedArray = noSorted.reduce(
    (acm: any[][], value, i, src) => {
      const pushValueToAccumulator = (toNextIterator?: boolean) => {
        if (toNextIterator) {
          iterator++;
          acm[iterator] = [];
        }
        acm[iterator].push(value);
      };

      if (i === 0) {
        pushValueToAccumulator();
      } else {
        const prevValue = src[i - 1];
        const { crash_point } = value;
        const isLoss = Number(crash_point) < positivePayout;
        const isPrevLoss = Number(prevValue.crash_point) < positivePayout;

        pushValueToAccumulator(!isLoss === isPrevLoss);
      }

      return [...acm];
    },
    [[]],
  );

  let deleteLastIndex: number | undefined;

  const result = sortedArray.reduce((acm: any[], value, i, src) => {
    const rowLength = (acm[i]?.length || 0) + maxRowNumber;
    if (value.length > rowLength) {
      const iterateAmount = value.length - rowLength;
      const allowableMaxLength = rowLength - maxRowNumber;
      for (let index = 1; index <= iterateAmount; index++) {
        const acmIterator = i + index;
        if (acm[acmIterator]) {
          acm[acmIterator].length += allowableMaxLength;
        } else {
          acm[acmIterator] = {
            length: allowableMaxLength - 1,
          };
        }
      }
    }

    if (acm[i]) {
      acm[i].length = rowLength;
      acm[i].value = value;
    } else {
      acm[i] = {
        length: rowLength,
        value,
      };
    }

    if (value.length - rowLength + i > maxCellNumber) {
      deleteLastIndex = i;
    } else if (i > maxCellNumber - 2 && !deleteLastIndex) {
      const deleteRowsAmount =
        (sortedArray[0]?.length || 4) +
        (sortedArray[1]?.length || 0) +
        (sortedArray[2]?.length || 0);

      deleteLastIndex = deleteRowsAmount;
    }

    return acm;
  }, []);

  let deleteIndex = 0;

  if (deleteLastIndex || deleteLastIndex === 0) {
    for (let index = 0; index < deleteLastIndex; index++) {
      deleteIndex += sortedArray[index].length;
    }
  }

  return {
    dots: result,
    deleteIndex,
  };
};
