import React, { FC, Suspense } from "react";

import LazyComponentLoader from "config/LazyComponentLoader";
import ProgressFallback from "components/fallbacks/Progress";

const HistoryModal = React.lazy(() => LazyComponentLoader(import("./Content")));
interface Props {
  onClose: () => void;
  id: string | null;
}

const Content: FC<Props> = ({ onClose, id }) => {
  return (
    <>
      {!!id && (
        <Suspense fallback={<ProgressFallback />}>
          <HistoryModal onClose={onClose} id={id} />
        </Suspense>
      )}
    </>
  );
};

export default Content;
