import React, { FC } from "react";
import clsx from "clsx";

import { positivePayout } from "../../History/StoryElement";
import css from "./style.module.scss";
interface Props {
  data: number[][];
}

const ExampleText: FC<Props> = ({ data }) => {
  return (
    <div className={css.root}>
      {data.map((value, i, src) => {
        const result: JSX.Element[][] = [];
        const lastCell = src.findIndex((unit) => !unit.length);

        if (value.length) {
          const jsx = value.map((num, i, src) => (
            <React.Fragment key={`@_${num}_${i}`}>
              <p
                className={clsx(
                  css.text,
                  {
                    [css.red]: num < positivePayout,
                  },
                  {
                    [css.green]: num >= positivePayout,
                  },
                )}>
                {num}
              </p>
              {i !== src.length - 1 && <div className={clsx(css.line)}></div>}
            </React.Fragment>
          ));

          result.push(jsx);

          if (i < lastCell - 1) {
            result.push([
              <div key={`→_Arrow_#${i}`} className={clsx(css.line)}></div>,
            ]);
          }
        }
        return <React.Fragment key={`@key#${i}`}>{result}</React.Fragment>;
      })}
    </div>
  );
};

export default ExampleText;
